@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Mono&display=swap');
/* header start */
.App header{
  display: flex;
  padding: 16px;
}
.App header input{
  font-family: 'Roboto';
  font-size: 12px;
  padding: 9px 15px;
  flex: 1;
  background: #F6F6F6;
  border-radius: 8px;
  border: 1px solid #C4C4C4;
  margin-right: 8px;
}
.App header input::placeholder{
  color: #C4C4C4;
}

.App header button{
  box-shadow: 0px 4px 4px 0px #00000040;
}

.App header button:nth-child(2){
  padding: 9px 18px;
  border: 1px solid #FFA715;
  font-family: 'Roboto';
  font-size: 12pt;
  background: #FFFFFF;
  color: #FFA715;
  border-radius: 8px;
  margin-right: 16px;
}
.App header button:nth-child(3){
  padding: 8px 33px;
  background: #FFA715;
  color: white;
  border-radius: 8px;
  border: none;
  margin-right: 16px;
}
.App header button:nth-child(4){
  padding: 8px 15px;
  border: 1px solid #FFA715;
  font-family: 'Roboto';
  font-size: 12pt;
  background: #FFFFFF;
  color: #FFA715;
  border-radius: 8px;
}
/* header end */
/* content start */
/* sidebar start */
.App .content{
  display: flex;
}
.App .content .sidebar{
  flex: 0.25;
  padding-left: 16px;
  padding-right: 16px;
}
.App .content .sidebar h3{
  font-family: 'Roboto';
  font-weight: bold;
  font-size: 12pt;
  margin: 0;
}
.App .content .sidebar ul{
  margin-left: -40px;
  margin-top: 16px;
}
.App .content .sidebar ul li{
  list-style: none;
  font-family: 'Roboto';
  font-size: 12pt;
  margin-bottom: 16px;
}
.App .content .sidebar ul li .icon{
  margin-right: 8px;
}
/* sidebar end */
/* table start */
.App .content .table{
  flex: 1;
  height: 525px;
  overflow: auto;
}
.App .content .table table{
  border-collapse: collapse;
  width: 100%;
}
.App .content .table table thead{
  background: #C4C4C4;
}
.App .content .table table thead td{
  padding: 8px;
  font-family: 'Roboto';
  font-size: 10pt;
  font-weight: bold;
  min-width: 120px;
  border-right: 1px solid white;
}
.App .content .table table tbody tr{
  border-bottom: 1px solid #C4C4C4;
}
.App .content .table table tbody tr:nth-child(2n){
  background: #F6F6F6;
}
.App .content .table table tbody td{
  padding: 8px;
  margin: 0;
  font-family: 'Roboto Mono';
  font-size: 10pt;
  border-right: 1px solid #C4C4C4;
}
/* table end */
/* content end */
/* footer start*/
.App footer{
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: #FFF3D0;
  display: flex;
  padding: 16px;
  flex-direction: column;
}
.App footer button{
  border: none;
  font-family: Roboto;
  font-size: 10pt;
  font-style: normal;
  font-weight: 700;
  line-height: 12px;
  letter-spacing: 0em;
  text-align: left;
  text-transform: uppercase;
  align-self: flex-end;
  background: none;
  cursor: pointer;
}
.App footer button .icon{
  margin-right: 8px;
}
/* footer end */